@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600&family=Sora:wght@300;400&display=swap);
:root {
  box-sizing: border-box;
  font-family: 'Sora', 'Roboto', sans-serif;
  font-size: calc(1vw + 0.6em);
}

*,
::before,
::after {
  box-sizing: inherit;
}

@media (min-width: 50em) {
  body {
    font-size: 1.125em;
  }
}

body {
  margin: 0;
}

input, textarea, button {
  font-family: inherit;
}
:root {
    --cell-size: 1.2em;
}

.cell {
    display: flex;
    justify-content: center;
    width: 1.2em;
    width: var(--cell-size);
    height: 1.2em;
    height: var(--cell-size);
    border-top: 1px solid var(--grid-border-color);
    border-left: 1px solid var(--grid-border-color);
}

.cell--transition {
    /*transition: .2s background;*/
}

.cell > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.cell__icon--normal {
    width: 55% !important;
}

.cell__icon--small {
    width: 40% !important;
}

.cell__icon--extra-small {
    width: 30% !important;
}

.cell.light-border {
    border-color: #f4f9ff;
}

.cell--wall {
    background: #456AB5;
}

.cell--visiting {
    background: #F8DD74;
    transition: none;
    border-color: #f4f9ff;
}

.cell--to-visit {
    background: #dbebff;
    border-color: #f4f9ff;
}

.cell--visited {
    background: #c1ddff;
    border-color: #f4f9ff;
}

.cell--path {
    background: #F8DD74;
}

.full-width-height {
    width: 100%;
    height: 100%;
}

.draggable {
    cursor: grab;
}

.draggable:active {
    cursor: grabbing;
}

:root {
    --grid-border-color: #bfd5ff;
}

.grid__wrapper {
    display: flex;
    justify-content: center;
}

.grid {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    background: #f4f9ff;
    border-right: 1px solid #bfd5ff;
    border-right: 1px solid var(--grid-border-color);
    border-bottom: 1px solid #bfd5ff;
    border-bottom: 1px solid var(--grid-border-color);
}

.grid__row {
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.dropdown {
    width: 100%;
    display: inline-block;
    position: relative;
    vertical-align: bottom;
}

.dropdown__label {
    display: flex;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    padding: 0.7em 2em 0.7em 0.8em;
    border: 0;
    border-radius: 0.5em;

    color: white;
    font-size: 0.6rem;
    font-weight: 200;
    text-shadow: 1px 1px #148;
}

.dropdown__label.enabled {
    background-color: #57b;
    box-shadow: 0 0.3em #148;
    cursor: pointer;
}

.dropdown__label.disabled {
    background-color: #7697d3;
    box-shadow: 0 0.3em #4b74b2;
}


.dropdown__label.enabled:hover {
    background-color: #456ab5;
    -webkit-transform: translateY(0.1em);
            transform: translateY(0.1em);
    box-shadow: 0 0.2em #148;
}

.dropdown__label.enabled.opened {
    background-color: #456ab5;
    -webkit-transform: translateY(0.3em);
            transform: translateY(0.3em);
    box-shadow: 0 0;
}

.dropdown__label::after {
    content: "";
    position: absolute;
    right: 1em;
    top: 1em;
    border: 0.3em solid;
    border-color: white transparent transparent;
}

.dropdown__label.enabled.opened::after {
    top: 0.7em;
    border-color: transparent transparent white;
}

.dropdown__label-label {
    font-size: 0.3rem;
}

.dropdown__label-value {
    margin-left: 0.4em;
}

.dropdown__menu {
    position: absolute;
    left: 0;
    right: 0;
    top: 2.1em;

    margin-top: 1em;

    min-width: 100%;
    max-height: 30em;
    overflow-y: auto;
    color: white;
    border-radius: .5em;
    background-color: #57b;
    box-shadow: 0 0.3em #148;
    font-weight: 200;
    font-size: 0.6rem;

}

.dropdown__empty-submenu {
    font-size: 0.5rem;
    padding: .8em 1.5em;
}

.dropdown__submenu {
    padding-left: 0;
    margin: 0;
    list-style-type: none;
}

.dropdown__submenu > li {
    padding: .5em 1.5em;
    margin-top: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}

.dropdown__submenu > li:hover {
    background-color: #779EEFFF;
}

.dropdown__submenu > li.selected {
    background-color: #148;
}

.button {
    display: flex;

    font-size: 0.6rem;
    text-shadow: 1px 1px #148;
    font-weight: 200;

    padding: 0.7em 2em;
    border: 0;
    border-radius: .5em;

    color: white;
}

.button > span.extra-right-margin {
    margin-left: 0.6em;
}

.button.enabled {
    background-color: #57b;
    box-shadow: 0 0.3em #148;
    cursor: pointer;
}

.button.disabled {
    background-color: #7697d3;
    box-shadow: 0 0.3em #4b74b2;
}

.button.enabled:hover {
    background-color: #456ab5;
    -webkit-transform: translateY(0.1em);
            transform: translateY(0.1em);
    box-shadow: 0 0.2em #148;
}

.button.enabled:active {
    background-color: #456ab5;
    -webkit-transform: translateY(0.3em);
            transform: translateY(0.3em);
    box-shadow: 0 0;
}

.button.enabled.checked {
    background-color: #456ab5;
    -webkit-transform: translateY(0.3em);
            transform: translateY(0.3em);
    box-shadow: 0 0;
}

.button.enabled.checked:hover {
    background-color: #57b;
}


.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 0 2em;
    background: #F8DD74;
}

.header__label {
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.01em;
    color: #DB6FD0;
    text-shadow: 1px 1px #b7a642;
}

.header__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.header__buttons > div + div {
    margin-left: 1.5em;
}

.header__buttons > div {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
}

@media (min-width: 35em) {
    .header {
        flex-direction: row;
    }
}

